@import url(https://fonts.googleapis.com/css?family=Petit+Formal+Script);
@import url(https://use.typekit.net/dns6ten.css);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed);
@import url(https://fonts.googleapis.com/css?family=Montserrat|Roboto+Slab);
html{scroll-behavior:smooth}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	
	min-width: 320px;
	min-height:568px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
};
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
};
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body{
	background: url(/janet-leon-portfolio-temp/static/media/background.fa663b07.png) no-repeat center center;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
  width: 100%;

	background-attachment: fixed;
	background-repeat: no-repeat;
}

.navigation{
	background: rgb(43, 42, 42);
		position: fixed;
		z-index: 2;
		height: 80px;
		margin-bottom: 0px;
		width: 277px;
		overflow:hidden;
		margin-top:0px;
	}

	.main{
		position:absolute;
		width: 277px;
		left: 22px;
	}
.container{
width: 100vw;
height: 100vh;
display: flex;
justify-content: center;
align-items: center;
text-align: center;
flex-direction: column;
box-sizing: border-box;
}



.outline-section {
	width: 50vh;
	height: 50vh;
	margin: 3px;
	border-radius: 100%;
	background: #e9d9d4c7;

	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	flex-direction: column;
	box-sizing: border-box;
}

header h1 {
	width: 90%;
    margin-top: 40px;
	font-family: Petit Formal Script;
	font-style: normal;
	font-weight: normal;
	font-size: 50px;
	text-align: center;
	letter-spacing: -0.07em;
	color: #ffffff;
	text-shadow: 3px 2px 1px rgba(0, 0, 0, 0.42);
}

header h2 {
	padding: 1px;
	margin-top: 0px;
	font-family: Prestige Elite Std;
	font-style: normal;
	font-weight: bold;
	line-height: 23px;
	font-size: 35px;
	text-align: center;
	letter-spacing: -0.05em;
	color: #494646;
}

p {
	font-family: Roboto Slab;
	font-style: normal;
	font-weight: bolder;
	font-size: 14px;
	letter-spacing: 0.03em;
	color: #9d9391;
    text-align: center;
    position: relative;
    top:-40px;
}

i {
	padding: 10px;
}

.fab {
    font-size: 20px;
    color: rgb(37, 35, 35);
}

.icons {
	margin-top: 10px;
}


